// src/details/File3.js
import { useState, useEffect } from 'react';
import { Tab, Tabs, Table } from 'react-bootstrap';
import Papa from 'papaparse';
//import CsvTable from '../components/CsvTable';

const File3 = () => {
    const [key, setKey] = useState('detail');
    const [pathwayData, setPathwayData] = useState([]);

    useEffect(() => {
        fetch('/data/bulk_pathway.csv')
            .then((response) => response.text())
            .then((text) => {
                Papa.parse(text, {
                    header: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        setPathwayData(results.data);
                    },
                });
            });
    }, []);

    return (
        <div>
            <h2>Pathway</h2>
            <hr />
            <h4>Descriptions</h4>
            <p>Detailed information about Pathway.</p>
            <br />
            <h4>Data Structure</h4>
            <h5>Features</h5>
            <h4>Data Distribution</h4>

            <h4>Files information</h4>
            <Tabs
                defaultActiveKey="detail"
                id="file-info"
                className="mb-3"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                justify
                >
                <Tab eventKey="detail" title="Detail">
                {pathwayData.length > 0 ? (
                        <Table striped bordered hover>
                            <thead>
                                <tr>
                                    {Object.keys(pathwayData[0]).map((header) => (
                                        <th key={header}>{header}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {pathwayData.map((row, index) => (
                                    <tr key={index}>
                                        {Object.values(row).map((value, i) => (
                                            <td key={i}>{value}</td>
                                        ))}
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                    ) : (
                        <div>Loading...</div>
                    )}
                </Tab>
                <Tab eventKey="compact" title="Compact">
                    Compact detail of this file
                </Tab>
                <Tab eventKey="column" title="Column">
                    Column values of the file
                </Tab>
            </Tabs>

        </div>
    );
};

export default File3;
